import axios from 'axios'
import { getToken, removeToken } from '@/utils/auth'

var baseURL
if (process.env.NODE_ENV === 'development') {
  baseURL = process.env.API_ROOT
} else {
  baseURL = "http://127.0.0.1:8001/botmind_api"
}

// baseURL = "https://api.ownthink.com"
baseURL = "http://localhost:8000/api/v1"

const service = axios.create({
  baseURL, // api 的 base_url
  timeout: 5000, // request timeout
  // `withCredentials` indicates whether or not cross-site Access-Control requests should be made using credentials
  withCredentials: false, // 决定跨域请求是否带上cookies, cookies也包含了这个页面下其他域名的cookie
})

// request interceptor
service.interceptors.request.use(
  config => {
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  // response => response,
  /**
   * 下面的注释为通过在response里，自定义code来标示请求状态
   * 当code返回如下情况则说明权限有问题，登出并返回到登录页
   * 如想通过 xmlhttprequest 来状态码标识 逻辑可写在下面error中
   * 以下代码均为样例，请结合自生需求加以修改，若不需要，则可删除
   */
  response => {
    const res = response.data
    console.log(response.cookies)
    // if (res.code !== 20000) {
      // Message({
      //   message: res.message,
      //   type: 'error',
      //   duration: 5 * 1000
      // })
      // // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
      // if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
      //   // 请自行在引入 MessageBox
      //   // import { Message, MessageBox } from 'element-ui'
      //   MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
      //     confirmButtonText: '重新登录',
      //     cancelButtonText: '取消',
      //     type: 'warning'
      //   }).then(() => {
      //     store.dispatch('FedLogOut').then(() => {
      //       location.reload() // 为了重新实例化vue-router对象 避免bug
      //     })
      //   })
      // }
      // return Promise.reject('error')
    // } else {
      if(res.msg) {
      }
      return response
    // }
  },
  error => {
    console.log(error.response) // for debug
    var msg
    if(error.response) {msg = error.response.data.msg}
    if(error.response.status == 403) {removeToken();console.log("removeToken")} 
    return Promise.reject(error)
  }
)

export default service



