<template>
<v-chart :options="force"/>
  <!-- <div id="myChart" :style="{width: '300px', height: '300px'}"></div> -->
</template>

<style>
.echarts {
  width: 100%;
  height: 100%;
}
</style>

<script>
import 'echarts/lib/chart/line'
import 'echarts/lib/component/polar'
// import 'echarts/lib/component/force'
import 'echarts/lib/chart/graph'

import {fetchEntity} from '@/api/apis';

export default {
//   components: {
//     'v-chart': ECharts
//   },
  data: function () {
    let entity="深圳大学"
    let data, nodes=[], links=[]
    fetchEntity({entity}).then(res => {
      data=res.data.       
      data = JSON.parse(data)      
    })

    console.log(data)
    nodes.push({name:entity})
    for (elem in data.data.avg) {
      nodes.push({name:elem[1]})
      links.push({source:entity, target:elem[1], name:elem[0]})
    } 

    console.log(data)


    data = []

    for (let i = 0; i <= 360; i++) {
        let t = i / 180 * Math.PI
        let r = Math.sin(2 * t) * Math.cos(2 * t)
        data.push([r, i])
    }

    // force

    var categories = [];
    for (var i = 0; i < 9; i++) {
        categories[i] = {
            name: '类目' + i
        };
    }

    // nodes = [
    //     {category:0, name: '乔布斯', value : 10, label: '乔布斯\n（主要）'},
    //     {category:1, name: '丽萨-乔布斯',value : 2},
    //     {category:1, name: '保罗-乔布斯',value : 3},
    //     {category:1, name: '克拉拉-乔布斯',value : 3},
    //     {category:1, name: '劳伦-鲍威尔',value : 7},
    //     {category:2, name: '史蒂夫-沃兹尼艾克',value : 5},
    //     {category:2, name: '奥巴马',value : 8},
    //     {category:2, name: '比尔-盖茨',value : 9},
    //     {category:2, name: '乔纳森-艾夫',value : 4},
    //     {category:2, name: '蒂姆-库克',value : 4},
    //     {category:2, name: '龙-韦恩',value : 1},
    // ]
    // links = [
    //     {source : '丽萨-乔布斯', target : '乔布斯', weight : 1, name: '女儿'},
    //     {source : '保罗-乔布斯', target : '乔布斯', weight : 2, name: '父亲'},
    //     {source : '克拉拉-乔布斯', target : '乔布斯', weight : 1, name: '母亲'},
    //     {source : '劳伦-鲍威尔', target : '乔布斯', weight : 2},
    //     {source : '史蒂夫-沃兹尼艾克', target : '乔布斯', weight : 3, name: '合伙人'},
    //     {source : '奥巴马', target : '乔布斯', weight : 1},
    //     {source : '比尔-盖茨', target : '乔布斯', weight : 6, name: '竞争对手'},
    //     {source : '乔纳森-艾夫', target : '乔布斯', weight : 1, name: '爱将'},
    //     {source : '蒂姆-库克', target : '乔布斯', weight : 1},
    //     {source : '龙-韦恩', target : '乔布斯', weight : 1},
    //     {source : '克拉拉-乔布斯', target : '保罗-乔布斯', weight : 1},
    //     {source : '奥巴马', target : '保罗-乔布斯', weight : 1},
    //     {source : '奥巴马', target : '克拉拉-乔布斯', weight : 1},
    //     {source : '奥巴马', target : '劳伦-鲍威尔', weight : 1},
    //     {source : '奥巴马', target : '史蒂夫-沃兹尼艾克', weight : 1},
    //     {source : '比尔-盖茨', target : '奥巴马', weight : 6},
    //     {source : '比尔-盖茨', target : '克拉拉-乔布斯', weight : 1},
    //     {source : '蒂姆-库克', target : '奥巴马', weight : 1}
    // ]

    return {
      force: {
        title: {
            text: 'Les Miserables',
            subtext: 'Default layout',
            top: 'bottom',
            left: 'right'
        },
        tooltip: {},
        legend: [{
            // selectedMode: 'single',
            data: categories.map(function (a) {
                return a.name;
            })
        }],
        animation: false,
        series : [
            {
                name: 'Les Miserables',
                type: 'graph',
                layout: 'force',
                data: nodes,
                links: links,
                categories: categories,
                symbolSize: 25,
                roam: true,
                label: {
                    normal: {
                        show: true,
                        position: 'inside'
                    }
                },
                force: {
                    repulsion: 100
                }
            }
        ]
      },
      polar: {
        title: {
          text: '极坐标双数值轴'
        },
        legend: {
          data: ['line']
        },
        polar: {
          center: ['50%', '54%']
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        },
        angleAxis: {
          type: 'value',
          startAngle: 0
        },
        radiusAxis: {
          min: 0
        },
        series: [
          {
            coordinateSystem: 'polar',
            name: 'line',
            type: 'line',
            showSymbol: false,
            data: data
          }
        ],
        animationDuration: 2000
      }
    }
  },
  created(){

  },
  mounted(){
    // this.drawLine();
  },
  methods: {
    drawLine(){
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 绘制图表
        myChart.setOption({
            title: { text: '在Vue中使用echarts' },
            tooltip: {},
            xAxis: {
                data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'graph',
                data: [5, 20, 36, 10, 10, 20]
            }]
        });
    }
  }
}
</script>



