<template>
<ul>
    <template v-for="items in items">
        <li :style="{marginRight: gutter+'px'}">
            <Card class="card" :style="{height: height+'px'}">
                <!-- <p slot="title">{{items.name}}</p> -->
                <!-- <Icon  :type="items.icon" size="50" style=" margin: 0 30px 5px 30px"/> -->
                <div class="title"><h4 v-html="items.name"></h4></div>
                <Icon v-if="icon" :custom="'i-icon iconfont '+items.icon" size="50" :color='color' style=" margin: 0 30px 5px 30px"/>
                <p class="desc">{{items.desc}}</p>
            </Card>
        </li>
    </template>
</ul>    
</template>

<script>
export default {
    name: "CardList",
    props: {
        items: Object, 
        icon: {
            type: Boolean,
            default: true
        },
        height: Number,
        color: String,
        gutter: Number,
    },    
}
</script>

<style scoped>
/* ul {
    padding: 20px 0;
} */

li {
    /* border: #eee solid 1px; */
    width: 147px;
    /* height: 157px; */
    margin: 0 10px -1px 0;
    cursor: pointer;
    float: left;
    display: list-item;
    list-style: none;
}


.card {
  height: 180px;
  overflow: hidden;
  margin: 30px 0;
}

.card *{
  
  margin: 0 auto;
  display: block;
}


.title {
    text-align: center;
    padding: 0 0 13px 0;
}

.desc {
    text-align: center;
    color: gray;
    font-size: 12px;
    padding-top: 10px;
    height: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
}
</style>



