<template>
<div>
    <Input v-model="inputtext" type="textarea" :rows="4" placeholder="Enter something..." />
    
    <Row :gutter="32">
        <Col span="20" class="demo-tabs-style1" style="background: #e3e8ee;padding:16px;">
            <Tabs type="card">
                <TabPane label="词性标注">
                    <Row>
                        <Col span="16">
                            <template v-for="posTp in speechTagging.sentence">
                                <Tag :color="posTp[1]" style="height:30px;font-size: 18px;!important">{{posTp[0]}}</Tag>                        
                            </template>
                        </Col>
                        <Col span="8">
                            <template v-for="posTp in speechTagging.parts2color">
                                <Tag :color="posTp[1]" style="height:30px;font-size: 18px;!important">{{posTp[0]}}</Tag>                        
                            </template>
                        </Col>
                    </Row>                        
                </TabPane>
                <TabPane label="实体识别">
                    <Row>
                        <Col span="16">
                            <template v-for="nerTp in nerObj.sentence">
                                <Tag :color="nerTp[1]" style="height:30px;font-size: 18px;!important">{{nerTp[0]}}</Tag>                        
                            </template>
                        </Col>
                        <Col span="8">
                            <template v-for="nerTp in nerObj.type2color">
                                <Tag :color="nerTp[1]" style="height:30px;font-size: 18px;!important">{{nerTp[0]}}</Tag>                        
                            </template>
                        </Col>
                    </Row>
                </TabPane>
                <TabPane label="标签三">标签三的内容</TabPane>
            </Tabs>
        </Col>
    </Row>
</div>
</template>

<script>
const colors = ['Brown','BurlyWood','CadetBlue','Chocolate','Coral','CornflowerBlue','Crimson','Cyan','DarkBlue','DarkCyan','DarkGoldenRod','DarkGray','DarkGreen','DarkKhaki','DarkMagenta','DarkOliveGreen','Darkorange','DarkOrchid','DarkRed','DarkSalmon','DarkSeaGreen','DarkSlateBlue','DarkSlateGray','DarkTurquoise','DarkViolet','DeepPink','DeepSkyBlue','DimGray']

export default {
    name: 'Cnlp',
    data() {
        return {
            inputtext: '乔布斯是苹果的前任CEO，库克是现任CEO。',
            pos: [
                'www/其它', './标点符号', 'panchuangai/其它', './标点符号', 'com/其它', '是/动词', '磐/其他专有名词', '创/动词', 'AI/其它', '的/助词', '官网/名词', '，/标点符号', '//标点符号', '//标点符号', '让/动词', '我们/代词', '一起/处所词', '来/动词', '唱/动词', '“/标点符号', '东方红/其他专有名词', '”/标点符号', '。/标点符号'
            ],
            ner: {
                'PER': "['乔布斯', '库克']", 
                'LOC': '[]', 
                'ORG': '[]', 
                'TIME': '[]', 
                'PRO': '[]', 
                'COM': "['苹果']", 
                'JOB': "['CEO']", 
                'TAG': "['B-PER', 'I-PER', 'I-PER', 0, 'B-COM', 'I-COM', 0, 0, 0, 'B-JOB', 'I-JOB', 'I-JOB', 0, 'B-PER', 'I-PER', 0, 0, 0, 0, 0, 0, 0]"
            },
            speechTagging: {
                sentence: [],
                parts2color: []
            },
            nerObj: {
                sentence: [],
                type2color: []
            },
        }
    },
    created() {
        [this.speechTagging.sentence, this.speechTagging.parts2color] = this.getPosList();
        [this.nerObj.sentence, this.nerObj.type2color] = this.getNerList()
    },
    methods: {
        getPosList() {
            let posTuple = [], match, matches
            for(let elem of this.pos) {
                matches = []
                let regex = /\//g
                let string = elem
                while (match = regex.exec(string)) {matches.push(match);}
                let i = matches[matches.length-1].index
                posTuple.push([elem.slice(0, i), elem.slice(i+1)])
            }

            let wordparts = new Set(posTuple.map(x=>x[1]))

            let l = [], k = 0
            for(let p of wordparts) {
                l.push([p, colors[k]])
                k++
            }
            let parts2color = new Map(l);

            posTuple = posTuple.map(x => [x[0], parts2color.get(x[1])])
            return [posTuple, [...parts2color]]
        },
        getNerList() {
            let sliceStart = 0, isStartWait = true
            let tagList = eval(this.ner.TAG)
            let sentence = this.inputtext
            let nerTuple = [], type        

            for(let [i, elem] of tagList.entries()) {

                if(isStartWait) {
                    if(elem != 0) {
                        isStartWait = false
                        if(i != 0) nerTuple.push([sentence.slice(sliceStart, i), type])

                        sliceStart = i
                        type = elem.split('-')[1]
                    }
                } else { // end wait...

                    if(elem == 0) {
                        isStartWait = true
                        nerTuple.push([sentence.slice(sliceStart, i), type])

                        sliceStart = i
                        type = null
                    } else if (elem.split('-')[0] == 'B') {
                        nerTuple.push([sentence.slice(sliceStart, i), type])
                        
                        sliceStart = i      
                        type = elem.split('-')[1]
                    } else {

                    }
                }
            }

            let types = new Set(nerTuple.map(x=>x[1]))

            let l = [], k = 0
            for(let p of types) {
                l.push([p, colors[k]])
                k++
            }
            let type2color = new Map(l);

            nerTuple = nerTuple.map(x => [x[0], type2color.get(x[1])])

            return [nerTuple, [...type2color]]
        }
    }
}
</script>
<style scoped>
.demo-tabs-style1 > .ivu-tabs-card > .ivu-tabs-content {
    height: 120px;
    margin-top: -16px;
}

.demo-tabs-style1 > .ivu-tabs-card > .ivu-tabs-content > .ivu-tabs-tabpane {
    background: #fff;
    padding: 16px;
}

.demo-tabs-style1 > .ivu-tabs.ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab {
    border-color: transparent;
}

.demo-tabs-style1 > .ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab-active {
    border-color: #fff;
}
</style>



