import request from '@/utils/request'

export function fetchEntity(params) {
  return request({
    url: '/kg/knowledge',
    method: 'get',
    params
  })
}  

export function fetchLogos(params) {
  return request({
    url: '/logos',
    method: 'get',
    params
  })
}  

export function postCorpus(data) {
  return request({
    url: '/upload_coupus/',
    method: 'post',
    data
  })
}  



