<template>
<div>
    <Carousel :value=0 loop>
        <template v-for="showCase in showCases">
            <CarouselItem>
                <div class="demo-carousel">
                    <Row type="flex" justify="center">
                        <Col span="2">
                            <div class="showImg">
                                <img :src="'/static/img/cases/'+showCase.name+'.png'" alt="">  
                            </div>                      
                        </Col>
                        
                    </Row>

                    <Row type="flex" justify="center"  >
                        <Col span="12">
                            <h4>客户简介：{{showCase.intro}}</h4>
                            <h5>产品介绍：{{showCase.product}}</h5>
                        </Col>
                    </Row>
                </div>
            </CarouselItem>
        </template>
    </Carousel>

    <Row type="flex" justify="center">
    <Col span="16">
        <Divider class="t2">合作公司</Divider>
    
        <Row type="flex" justify="space-around" :gutter="16">
            <template v-for="case1 in cases">
                <Col span="5">
                    <img :src="'/static/img/cases/'+case1+'.png'" alt="">
                    <p></p>
                </Col>
            </template>
        </Row>

        <Divider class="t2">客户评论</Divider>
        <Row type="flex" justify="space-around" :gutter="16">
            <Col span="18">
            <CardList :items='comments' height=200 />
            </Col>
        </Row>        
    </Col>
</Row>
</div>
</template>
<script>
let cases = ['acc5', 'aliyun', 'chainstack', 'ctc', 'fangzheng', 'huiSZ', 'jinzheng', 'julyedu', 'ledou', 'leifeng', 'maxfun', 'mircoeco', 'tcyun', 'youke', 'youtiao']

export default {
    name: 'Case',
    data() {
        return{
            cases: cases,
            showCases: [
                {name:'acc5', intro:'会计学堂是深圳快学教育发展有限公司旗下，致力于为广大会计人员提供高质量的会计实操、税务实操、注会CPA等各类培训。', product: '针对课程售前和答疑，为其量身定制专业版的智能客服系统，大大降低了人工成本，提高了客户的转换率，增加了课程销量。'},
                {name:'youke', intro:'UOOC联盟作为首个全国地方高校优质MOOC课程资源共享平台，促进我国高等教育均衡化发展。', product: '针对UOOC的课程问题，BotMind以更高的准确率、更精准的推荐、7*24小时不间断服务，为企业创造更多的价值。'},
                {name:'julyedu', intro:'七月在线是国内领先的人工智能教育平台，是国内最早、最专业、规模最大的AI教育机构。', product: '针对课程的推广与销售问题，磐小企智能客服为七月在线提供更精准的用户推荐、更完善的售前服务、更专业的课程咨询、更智能的销售体系。帮助企业提升服务效率、降低企业成本。'},
                {name:'maxfun', intro:'一满乐是国内最专业的做数据驱动的公司，致力于为企业提供建立在机器学习和深度学习技术基础上的数据分析服务。', product: '针对一满乐的业务，磐创为其量身定制企业知识图谱和数据解决方案，帮助企业大大降低了管理和运营成本。'},
            ],
            comments: [
                {name: '会计学堂<br/>刘经理', desc: '磐创帮我们重新定义了智能客服解决方案，节省了企业很大的成本，重塑了服务体系，相信在未来很长一段时间我们将携手磐创共同打造更好的课程、更棒的服务！'},
                {name: '普罗米修斯<br/>赵总', desc: '和磐创合作的这一年多，让我体会到了真正的“用心服务”，智能客服公司有很多，而磐创的服务是唯一的！'},
                {name: '七月在线<br/>张总监', desc: '他们的聊天机器人真的准确率很高，对话也非常的自然，有很多为我们定制化的场景，望磐创越做越好！'},
                {name: 'Uooc联盟<br/>陈总', desc: '使用BotMind一年多的时间，极大的减轻了我们线上工作人员的压力，推荐系统也非常的人性化，感谢磐创的产品和服务，感谢你们的付出。'},
                {name: '一满乐<br/>欢哥', desc: '一起合作的这段时间，切身体会到了AI解决方案的强大，他们更懂AI，我们更懂数据，这样的合作让双方彼此都更好。'},
                {name: '岭南科技<br/>黄总', desc: '磐创重新定义了人机交互方式，也让我们对自己的产品和服务有了重新的思考。'},
            ]
        }
    }
}
</script>
<style scoped>
img {
    width: 100%;
}

.demo-carousel {
    height: 400px;
    /* background: coral; */
    color:white;
    padding: 75px 0 0 0;
    background-image: url('~@/assets/img/黑色背景-2.png');
}

.showImg img{
    /* border: 1px; */
    border-radius: 10px;
    margin-bottom: 20px;
}
</style>



